/*
	JSON-to-Go
	by Matt Holt

	https://github.com/mholt/json-to-go

	A simple utility to translate JSON into a Go type definition.
*/

import pluralize from 'pluralize';
import { Language } from './types.d';

const uncapitalize = (text: string) => {
    return text.charAt(0).toLowerCase() + text.slice(1);
}

export const translate = (target: Language, source: Language, text: string): { text: string, error: string } | undefined => {

	if (source === Language.JSON && target === Language.Kotlin) {
		let { go, error } = jsonToGo(text);
		return {
			text: go,
			error: error
		}

	} else if (source === Language.Kotlin && target === Language.TypeScript) {
		return {
			text: text.replace(/data class/g, "export type").replace(/val /g, "").replace(/\t@JsonProperty\(.*\n/g, "")
				.replace(/\)/g, "};").replace(/\(/g, " = {").replace(/(List)<(.*)>/g, "$2[]")
				.replace(/: String/g, ": string").replace(/: Int|: Float/g, ": number").replace(/: Any/g, ": any"),
			error: ''
		}

	} else {
		throw new Error("Illegal state")
	}
}

const snakeToCamel = (text: string) => {
	return text.replace(/([-_]\w)/g, g => g[1].toUpperCase());
}

const jsonToGo = (json: string, typename?: string, flatten = true) => {
	let data;
	let scope;
	let go = "";
	let tabs = 0;

	const seen: any = {};
	const stack: string[] = [];
	let accumulator = "";
	let innerTabs = 0;
	let parent = "";

	try {
		data = JSON.parse(json.replace(/:(\s*\d*)\.0/g, ":$1.1")); // hack that forces floats to stay as floats
		scope = data;
	}
	catch (e) {
		return {
			go: "",
			error: e.message
		};
	}

	typename = format(typename || "AutoGenerated");
	append(`data class ${typename}`);

	parseScope(scope);

	return {
		go: flatten
			? go += accumulator
			: go
	};


	function parseScope(scope: any, depth = 0) {
		if (typeof scope === "object" && scope !== null) {
			if (Array.isArray(scope)) {
				let sliceType: any;
				const scopeLength = scope.length;

				for (let i = 0; i < scopeLength; i++) {
					const thisType = goType(scope[i]);
					if (!sliceType)
						sliceType = thisType;
					else if (sliceType !== thisType) {
						sliceType = mostSpecificPossibleGoType(thisType, sliceType);
						if (sliceType === "Any")
							break;
					}
				}

				const slice = flatten && ["struct", "slice"].includes(sliceType)
					? `List<${format(parent)}>`
					: `List`;

				if (flatten && depth >= 2)
					appender(slice);
				else
					append(slice)
				if (sliceType === "struct") {
					const allFields: any = {};

					// for each field counts how many times appears
					for (let i = 0; i < scopeLength; i++) {
						const keys = Object.keys(scope[i])
						for (let k in keys) {
							let keyname = keys[k];
							if (!(keyname in allFields)) {
								allFields[keyname] = {
									value: scope[i][keyname],
									count: 0
								}
							}
							else {
								const existingValue = allFields[keyname].value;
								const currentValue = scope[i][keyname];

								if (compareObjects(existingValue, currentValue)) {
									const comparisonResult = compareObjectKeys(
										Object.keys(currentValue),
										Object.keys(existingValue)
									)
									if (!comparisonResult) {
										keyname = `${keyname}_${uuidv4()}`;
										allFields[keyname] = {
											value: currentValue,
											count: 0
										};
									}
								}
							}
							allFields[keyname].count++;
						}
					}

					// create a common struct with all fields found in the current array
					// omitempty dict indicates if a field is optional
					const keys = Object.keys(allFields), struct: any = {}, omitempty: any = {};
					for (let k in keys) {
						const keyname = keys[k], elem = allFields[keyname];

						struct[keyname] = elem.value;
						omitempty[keyname] = elem.count !== scopeLength;
					}
					parseStruct(depth + 1, innerTabs, struct, omitempty); // finally parse the struct !!
				}
				else if (sliceType === "slice") {
					parseScope(scope[0], depth)
				}
				else {
					if (flatten && depth >= 2) {
						// appender(`<${sliceType}>&&&` || "interface{}");
						appender(`<${sliceType}>,\n` || "Any");
					} else {
						append(`<${sliceType}>` || "Any");
					}
				}
			}
			else {
				if (flatten) {
					if (depth >= 2) {
						appender(format(parent))
						// appender(",!!!\n")
						appender(",\n")
					}
					else {
						append(format(parent))
					}
				}
				parseStruct(depth + 1, innerTabs, scope);
			}
		}
		else {
			if (flatten && depth >= 2) {
				appender(goType(scope));
				// appender(",888\n")
				appender(",\n")
			}
			else {
				append(goType(scope));
			}
		}
	}

	function parseStruct(depth: any, innerTabs: any, scope: any, omitempty: any = null) {
		if (flatten) {
			stack.push(
				depth >= 2
					? "\n\n"
					: ""
			)
		}

		if (flatten && depth >= 2) {
			const parentType = `data class ${format(parent)}`;
			const scopeKeys = formatScopeKeys(Object.keys(scope));

			// this can only handle two duplicate items
			// future improvement will handle the case where there could
			// three or more duplicate keys with different values
			if (parent in seen && compareObjectKeys(scopeKeys, seen[parent])) {
				stack.pop();
				return
			}
			seen[parent] = scopeKeys;

			appender(`${parentType}(\n`);
			++innerTabs;
			const keys = Object.keys(scope);
			for (let i in keys) {
				const keyname = getOriginalName(keys[i]);

				const finalKeyname = uncapitalize(snakeToCamel(format(keyname)));

				// annotation
				// if (format(keyname) !== keyname || finalKeyname !== keyname) {
				if (finalKeyname !== keyname) {
					indenter(tabs);
					appender('@JsonProperty("' + keyname);
					if (omitempty && omitempty[keys[i]] === true) {
						appender(',omitempty');
					}
					// appender('"`\n');
					appender('")\n');
				}

				// const typename = format(keyname)
				indenter(tabs);
				const typename = finalKeyname
				appender("val " + typename + ": ");

				// type
				parent = pluralize.singular(typename)
				parseScope(scope[keys[i]], depth);
			}
			indenter(--innerTabs);
			// appender("fghj)");
			appender(")");
		}
		else {
			append("(\n");
			++tabs;
			const keys = Object.keys(scope);
			for (let i in keys) {
				const keyname = getOriginalName(keys[i]);

				const finalKeyname = uncapitalize(snakeToCamel(format(keyname)));

				// annotation
				if (keyname !== finalKeyname) {
				// if (format(keyname) !== keyname || keyname !== finalKeyname) {
					indent(tabs);
					append('@JsonProperty("' + keyname);
					if (omitempty && omitempty[keys[i]] === true) {
						append(',omitempty');
					}
					append('")\n');
				}

				// variable name
				indent(tabs);
				const typename = finalKeyname;
				append("val " + typename + ": ");

				// type
				parent = pluralize.singular(typename)
				parseScope(scope[keys[i]], depth);
				append(",\n");
			}
			indent(--tabs);
			append(")");
		}
		if (flatten)
			accumulator += stack.pop();
	}

	function indent(tabs: any) {
		for (let i = 0; i < tabs; i++)
			go += '\t';
	}

	function append(str: string) {
		go += str;
	}

	function indenter(tabs: any) {
		for (let i = 0; i < tabs; i++)
			stack[stack.length - 1] += '\t';
	}

	function appender(str: string) {
		stack[stack.length - 1] += str;
	}

	// Sanitizes and formats a string to make an appropriate identifier in Go
	function format(str: string) {
		if (!str)
			return "";
		else if (str.match(/^\d+$/))
			str = "Num" + str;
		else if (str.charAt(0).match(/\d/)) {
			const numbers: any = {
				'0': "Zero_", '1': "One_", '2': "Two_", '3': "Three_",
				'4': "Four_", '5': "Five_", '6': "Six_", '7': "Seven_",
				'8': "Eight_", '9': "Nine_"
			};
			str = numbers[str.charAt(0)] + str.substr(1);
		}
		return toProperCase(str).replace(/[^a-z0-9]/ig, "") || "NAMING_FAILED";
	}

	// Determines the most appropriate Go type
	function goType(val: any) {
		if (val === null)
			return "Any";

		switch (typeof val) {
			case "string":
				if (/^[0-9a-f]{8}-[0-9a-f]{4}-[0-5][0-9a-f]{3}-[089ab][0-9a-f]{3}-[0-9a-f]{12}$/i.test(val))
					return "UUID"
				else if (/\d{4}-\d\d-\d\dT\d\d:\d\d:\d\d(\.\d+)?(\+\d\d:\d\d|Z)/.test(val))
					return "time.Time";
				else
					return "String";
			case "number":
				if (val % 1 === 0)
					return "Int";
				else
					return "Float";
			case "boolean":
				return "Bool";
			case "object":
				if (Array.isArray(val))
					return "slice";
				return "struct";
			default:
				return "Any";
		}
	}

	// Given two types, returns the more specific of the two
	function mostSpecificPossibleGoType(typ1: string, typ2: string) {
		if (typ1.substr(0, 5) === "float"
			&& typ2.substr(0, 3) === "int")
			return typ1;
		else if (typ1.substr(0, 3) === "int"
			&& typ2.substr(0, 5) === "float")
			return typ2;
		else
			return "Any";
	}

	// Proper cases a string according to Go conventions
	function toProperCase(str: string) {
		// ensure that the SCREAMING_SNAKE_CASE is converted to snake_case
		if (str.match(/^[_A-Z0-9]+$/)) {
			str = str.toLowerCase();
		}

		// https://github.com/golang/lint/blob/5614ed5bae6fb75893070bdc0996a68765fdd275/lint.go#L771-L810
		const commonInitialisms = [
			"ACL", "API", "ASCII", "CPU", "CSS", "DNS", "EOF", "GUID", "HTML", "HTTP",
			"HTTPS", "ID", "IP", "JSON", "LHS", "QPS", "RAM", "RHS", "RPC", "SLA",
			"SMTP", "SQL", "SSH", "TCP", "TLS", "TTL", "UDP", "UI", "UID", "UUID",
			"URI", "URL", "UTF8", "VM", "XML", "XMPP", "XSRF", "XSS"
		];

		return str.replace(/(^|[^a-zA-Z])([a-z]+)/g, function (unused, sep, frag) {
			if (commonInitialisms.indexOf(frag.toUpperCase()) >= 0)
				return sep + frag.toUpperCase();
			else
				return sep + frag[0].toUpperCase() + frag.substr(1).toLowerCase();
		}).replace(/([A-Z])([a-z]+)/g, function (unused, sep, frag) {
			if (commonInitialisms.indexOf(sep + frag.toUpperCase()) >= 0)
				return (sep + frag).toUpperCase();
			else
				return sep + frag;
		});
	}

	function uuidv4() {
		return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
			var r = Math.random() * 16 | 0, v = c === 'x' ? r : ((r & 0x3) | 0x8);
			return v.toString(16);
		});
	}

	function getOriginalName(unique: string) {
		const reLiteralUUID = /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i
		const uuidLength = 36;

		if (unique.length >= uuidLength) {
			const tail = unique.substr(-uuidLength);
			if (reLiteralUUID.test(tail)) {
				return unique.slice(0, -1 * (uuidLength + 1))
			}
		}
		return unique
	}

	function compareObjects(objectA: any, objectB: any) {
		const object = "[object Object]";
		return Object.prototype.toString.call(objectA) === object
			&& Object.prototype.toString.call(objectB) === object;
	}

	function compareObjectKeys(itemAKeys: any, itemBKeys: any) {
		const lengthA = itemAKeys.length;
		const lengthB = itemBKeys.length;

		// nothing to compare, probably identical
		if (lengthA === 0 && lengthB === 0)
			return true;

		// duh
		if (lengthA !== lengthB)
			return false;

		for (let item of itemAKeys) {
			if (!itemBKeys.includes(item))
				return false;
		}
		return true;
	}

	function formatScopeKeys(keys: any) {
		for (let i in keys) {
			keys[i] = format(keys[i]);
		}
		return keys
	}
}

